.about {
    padding: 2rem;
    background-color: #ecf0f1;
    color: #2c3e50;
}

.about-title {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #34495e;
    text-align: center;
}

.about-content {
    position: relative;
}

.about-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    float: right; /* Hace que la imagen flote a la derecha del texto */
    margin-left: 2rem;
    margin-bottom: 0.5rem;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); /* Difuminado en el borde */
}

.about-text {
    font-size: 1.2rem;
    line-height: 1.9;
    text-align: justify; /* Justifica el texto para un aspecto más alineado */
}
